import { IconName } from "../Icon/Icon";
import { Margins } from "../styleguide";

export type MediaObjectVariants = "Thumbnail" | "Avatar" | "Icon";
export type MediaObjectPosition = "left" | "right";

export interface StyledMediaObjectProps extends Margins {
	alignment?: MediaObjectPosition
}

export interface MediaObjectProps extends StyledMediaObjectProps {
	dataQA: string
	className?: string
	variant?: MediaObjectVariants
	avatarText?: string
	avatarBackgroundColor?: string
	strokeColor?: string
	imageSource?: string
	icon?: IconName
	text?: React.ReactNode
	subtext?: React.ReactNode
}
